import { ref } from 'vue';
import mapboxgl from "mapbox-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
export default {
  name: "eventDetailCard",
  props: ['visible', 'info'],

  setup() {
    const mapDialogVisible = ref(false);
    const mapDialogLoading = ref(false);
    const accessToken = process.env.VUE_APP_MAP_BOX_ACCESS_TOKEN;
    const mapStyle = process.env.VUE_APP_MAP_BOX_STYLE;

    function initMap(lng, lat) {
      mapboxgl.accessToken = accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer",
        center: [lng, lat],
        style: mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker();
      marker.setLngLat([lng, lat]).addTo(map);
    }

    function showMap(lng, lat) {
      mapDialogVisible.value = true;
      mapDialogLoading.value = true;
      setTimeout(function () {
        initMap(lng, lat);
        mapDialogLoading.value = false;
      }, 2000);
    }

    function beforeMapDialogClose() {
      mapDialogVisible.value = false;
      mapDialogLoading.value = false;
    }

    return {
      mapDialogLoading,
      mapDialogVisible,
      showMap,
      beforeMapDialogClose
    };
  },

  methods: {
    close() {
      this.$emit('close');
    },

    rsvp() {
      this.$emit('rsvp', this.info);
    },

    beforeClose(done) {
      this.$emit('close');
      done();
    }

  }
};